import { Dispatch } from 'redux';
import { MenuActions } from './type';

export const selectMenuAction = (selected: string) => async (
  dispatch: Dispatch
) => {
    dispatch({ type: MenuActions.MENU_SELECTED,  selected});
};

export const setBlinkMenuAction = (blink: string) => async (
  dispatch: Dispatch
) => {
    dispatch({ type: MenuActions.MENU_BLINK,  blink:''});
    setTimeout(()=>{
      dispatch({ type: MenuActions.MENU_BLINK,  blink});
    },1)
    
};
