import { handleActions } from 'redux-actions';
import { User, SessionActions } from './type';
import { ActionType } from 'typesafe-actions';

const initialUser = {
  id: '',
  username: '',
  name: '',
  password: '',
  language: '',
  role: '',
};
export const initialState = {
  loading: false,
  authenticated: false,
  user: initialUser,
};
export type SessionState = typeof initialState;

const sessionReducer = handleActions<SessionState, ActionType<any>>(
  {
    [SessionActions.LOGIN_SUCCESS]: (state, { payload: { user } }) => {
      console.log(user);
      return {
        ...state,
        authenticated: true,
        user,
        loading: false,
      };
    },
    [SessionActions.LOGIN_FAILURE]: state => {
      return { ...state, authenticated: false, user: initialUser, permissions: [], loading: false };
    },
    [SessionActions.LOGOUT]: state => {
      return { ...state, authenticated: false, user: initialUser, permissions: [], loading: false };
    },
  },
  initialState
);

export default sessionReducer;
