import * as React from 'react';
import LoginPage from '../pages/Login/LoginPageConnected';
import HomePage from '../pages/Home/HomePage';
import { Routes } from './AppUrls';

export interface IBreadcrumbs {
  label: string;
  to?: string;
}

type RouteType = {
  path: string;
  component: React.ComponentType<any>;
  breadcrumbs?: IBreadcrumbs[];
  needAuth: boolean;
};

const routes: RouteType[] = [
  {
    path: Routes.ROOT,
    component: LoginPage,
    needAuth: false,
  },
  {
    path: Routes.LOGIN,
    component: LoginPage,
    needAuth: false,
  },
  {
    path: Routes.HOME,
    component: HomePage,
    needAuth: true,
  },
];

export default routes;
