import React from 'react';
import './DottedTree.scss';
import {FiDisc} from 'react-icons/fi';
import classNames from 'classnames';

interface IDottedTreeProps {
  active?: boolean;
  selected?: boolean;
}
const DottedTree = ({
  active,
  selected,
}: IDottedTreeProps) => (
  <div className={classNames('dotted-body',{active, selected})}>
    <div className="dotted-up" />    
    <div className="dotted-down" />
    <span className="tree-pointer"><FiDisc /></span>
  </div>

);

export default DottedTree;
