import React from 'react';
import './Button.scss';
import classNames from 'classnames';
import { FaTimes } from 'react-icons/fa';

export enum EButtonType {
  default = 'primary',
  primary = 'primary',
  secondary = 'secondary',
  light = 'light',
  transparent = 'transparent',
  link = 'link',
}
interface IButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  onClick?: (e: React.MouseEvent<HTMLElement>) => void;
  children: React.ReactNode;
  buttonType?: EButtonType;
  className?: string;
  block?: boolean;
  disabled?: boolean;
  loading?: boolean;
}
const Button = ({
  onClick,
  children,
  buttonType = EButtonType.default,
  className = '',
  block,
  type = 'button',
  loading = false,
  ...restProps
}: IButtonProps) => (
  <button
    onClick={onClick}
    type={type}
    className={classNames('button', buttonType, className, { block: block })}
    {...restProps}
  >
    {loading && (
      <FaTimes />
    )}
    {children}
  </button>
);

export default Button;
