import React, { Component } from 'react';
import DateRangePicker from 'react-bootstrap-daterangepicker';
import 'bootstrap-daterangepicker/daterangepicker.css';
import { TextInput } from '..';
import moment, { Moment } from 'moment';
import { IDateRangeProps, DateRanges } from '../types';
import { DateFormats } from '../../../../helpers/DateHelpers';

const ranges: DateRanges = {
  Today: [moment().startOf('day'), moment()],
  Yesterday: [
    moment().startOf('day').subtract(1, 'days'),
    moment().endOf('day').subtract(1, 'days'),
  ],
  'Last 7 Days': [moment().startOf('day').subtract(6, 'days'), moment().endOf('day')],
  'This Month': [moment().startOf('month'), moment().endOf('month')],
  'Last Month': [
    moment().subtract(1, 'month').startOf('month'),
    moment().subtract(1, 'month').endOf('month'),
  ],
};

class DateRange extends Component<IDateRangeProps> {
  static defaultProps = {
    opensTo: 'left',
    dropsTo: 'down',
    maxDate: moment().endOf('day'),
    withRanges: true,
    maxSpan: { days: 59 },
  };

  handleApply = (event: any, picker: any) => {
    this.handleRangeChanged(picker.startDate, picker.endDate);
  };

  handleCancel = (event: any, picker: any) => {
    this.handleRangeChanged(picker.startDate, picker.endDate);
  };

  handleEvent = (event: any, picker: any) => {
    let { startDate, endDate } = picker;
    const { haveClear } = this.props;
    if (haveClear && event.type === 'cancel') {
      startDate = undefined;
      endDate = undefined;
    }
    this.handleRangeChanged(startDate, endDate);
  };

  handleRangeChanged = (from: Moment, to: Moment) => {
    const {
      form: { setFieldValue },
      fromName,
      toName,
    } = this.props;
    const fromLabel = fromName || 'from';
    const toLabel = toName || 'to';
    setFieldValue(fromLabel, from ? from.format('X') : undefined);
    setFieldValue(toLabel, to ? to.format('X') : undefined);
  };

  render() {
    const {
      field,
      form,
      startDate,
      endDate,
      opensTo,
      dropsTo,
      placeholder,
      maxDate,
      withRanges,
      maxSpan,
      haveClear,
      timePicker = false,
    } = this.props;
    const start = startDate ? startDate.format(DateFormats.RegularDateTime) : '';
    const end = endDate ? endDate.format(DateFormats.RegularDateTime) : '';
    let label = start;
    if (start !== end) {
      label = `${label} - ${end}`;
    }

    const locale = {
      format: DateFormats.RegularDateTime,
      separator: ' - ',
      weekLabel: 'W',
      daysOfWeek: moment.weekdaysMin(),
      monthNames: moment.monthsShort(),
      firstDay: moment.localeData().firstDayOfWeek(),
      cancelLabel: haveClear ? 'Clear' : 'Cancel',
    };
    return (
      <DateRangePicker
        startDate={startDate}
        endDate={endDate}
        maxDate={maxDate}
        timePicker={timePicker}
        timePicker24Hour={timePicker}
        timePickerSeconds={timePicker}
        containerStyles={{ display: 'block' }}
        locale={locale}
        opens={opensTo}
        drops={dropsTo}
        maxSpan={maxSpan}
        ranges={withRanges ? ranges : undefined}
        alwaysShowCalendars
        onEvent={this.handleEvent}
      >
        <TextInput
          field={{ ...field, value: label }}
          placeholder={placeholder}
          form={form}
          icon={'calendar'}
        />
      </DateRangePicker>
    );
  }
}

export default DateRange;
