import React, { Fragment } from 'react';
import { FormattedNumber } from 'react-intl';
import { urlHelpers } from '../../config/AppUrls';
import { minimumFractionDigits, TransactionTypeStatus } from '../../config/AppConstants';
import { StatusLabel, StatusDot } from '../../components/commons';
import { isEmpty, camelCase } from 'lodash';
import classNames from 'classnames';


export const amountFormatter = (amount: number, currency?: string) => {
  if (typeof amount === 'undefined' || amount === null) return '';
  return (
    <Fragment>
      {currency && <span>{currency} &nbsp;</span>}
      <FormattedNumber minimumFractionDigits={minimumFractionDigits} value={amount} />
    </Fragment>
  );
};

export const requiredFormatter = (label: string) => {
  return (
    <Fragment>
      {label} <span className="required-asterix">*</span>
    </Fragment>
  );
};

export const bracketFormatter = (textValue: string, bracketValue: string | number) => {
  if (textValue && !isEmpty(textValue)) return `${textValue} (${bracketValue})`;
  return '';
};

export const pmImageFormatter = (code: string, name: string = '', showTooltip: boolean = false) => {
  const tooltip = name || code;
  if (code === null || code === undefined || code.trim() === '') {
    return <span></span>;
  }
  return (
    <>
      <img
        src={urlHelpers.getPaymentMethodImage(code)}
        className="payment-method-logo"
        alt={name}
        onError={urlHelpers.handleErrorImage}
        key={code}
        data-tip
        data-for={code} 
      />
      
    </>
  );
};


export const merchantStatusLimitFormatter = (status: string, value: number) => {
  return (
    <div className="text-status">
      {statusDotFormatter(status)} <span className="limit-value">{value}</span>
    </div>
  );
};

export const statusFormatter = (status: string) => {
  return <StatusLabel key={status} status={status} />;
};

export const statusSelectFormatter = (
  status: string,
  onClose?: (status: string) => void,
  forcePrimary: boolean = false
) => {
  return (
    <StatusLabel
      key={status}
      status={status}
      className="options__container__item"
      withClose
      onClose={onClose}
      forcePrimary={forcePrimary}
    />
  );
};

export const statusDotFormatter = (status: string) => <StatusDot status={status} />;

export const emptyFormatter = (value: any) =>
  (typeof value === 'string' && value.trim() === '') || value === null || value === undefined
    ? ''
    : value;

export const isXML = (data: string) => {
  const doc = new DOMParser().parseFromString(data, 'text/xml');
  return doc.body === null;
};

export const arrayFomatter = (data: Array<any>) => {
  return data.join(', ');
};

export const xmlFormatter = (data: string) => {
  const PADDING = ' '.repeat(2); // set desired indent size here
  const reg = /(>)(<)(\/*)/g;
  let pad = 0;
  const xml = data.replace(reg, '$1\r\n$2$3');

  return xml
    .split('\r\n')
    .map(node => {
      let indent = 0;
      if (node.match(/.+<\/\w[^>]*>$/)) {
        indent = 0;
      } else if (node.match(/^<\/\w/) && pad > 0) {
        pad -= 1;
      } else if (node.match(/^<\w[^>]*[^/]>.*$/)) {
        indent = 1;
      } else {
        indent = 0;
      }
      pad += indent;

      return PADDING.repeat(pad - indent) + node;
    })
    .join('\r\n');
};

export const jsonFormatter = (json: string) => {
  return json && JSON.stringify(JSON.parse(json), null, 2);
};

export const isValidJson = (json: string) => {
  try {
    JSON.parse(json);
  } catch (e) {
    return false;
  }
  return true;
};

export const urlDecode = (data: string) => {
  return data && decodeURIComponent(data.replace(/\+/g, ' '));
};

export const depositReasonFormatter = (reason: string) => {
  const label = `options_${reason}`;
  return camelCase(label);
};
