import React, { useEffect } from 'react';
import { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom';
//import IconLibrary from './config/IconLibrary';
import { withTranslation, WithTranslation } from 'react-i18next'; 
import { Loading } from './components/commons';
import { ModalContainer } from './components';
import { start } from './redux/mock/action';
import { fetchAdd, fetchCommit, setGlobalStateAction } from './redux/global/action';
import routes from './config/Routes';
import { Routes } from './config/AppUrls';
import NotFound from './pages/NotFound/NotFound';
import { IntegrityConnect } from './integrity/connect';
import GlobalToastContainer from './components/ToastContainer/GlobalToastContainer';

//IconLibrary.init();

interface appProps extends WithTranslation {   
  isLaunched: boolean;
  isLoading: boolean; 
  isAuthenticated: boolean;  
  fetchAdd: typeof fetchAdd;
  fetchCommit: typeof fetchCommit;
  setGlobalState: typeof setGlobalStateAction;
}

const App = (props: appProps) => {
  const { isAuthenticated, isLoading, isLaunched, fetchAdd, fetchCommit } = props; 

  useEffect(() => {
    if (!isLaunched && !isLoading) {
      //start(true);
      start();
      //fetchAdd('startApp');
      //fetchCommit('START');
    }
  });

  return ( 
    <BrowserRouter>
      <Switch>
        {routes.map(({ breadcrumbs = [], needAuth, ...route }) => (
          <Route
            exact
            key={route.path}
            path={route.path}
            render={props => {
              if (!needAuth || (needAuth && isAuthenticated)) {
                return <route.component />;
              } else {
                return <Redirect to={Routes.LOGIN} />;
              }
            }}
          />
        ))}
        <Route component={NotFound} />
      </Switch>
      <ModalContainer />
      <GlobalToastContainer />
      <Loading />
    </BrowserRouter>
  );
};

const appTranslated = withTranslation()(App);

export default IntegrityConnect(appTranslated, [
  'fetchAdd',
  'fetchCommit',
  'setGlobalState',
  'isLaunched',
  'isLoading',
  'isAuthenticated',
]);
