import React, { ReactElement } from 'react';
import classNames from 'classnames';
import { isObject } from 'lodash';
import { ErrorMessage, connect } from 'formik';
import { requiredFormatter } from '../../../helpers/formatters';
import { getChildrens, nonProps } from '../../../integrity/helpers';

const FieldContent = (props: any) => {
  const {
    field: { name, value, onChange, onBlur },
    form: { touched, errors },
    className,
    label,
    required,
    handleOnBlur,
    handleOnChange,
    children,
    onFieldBlur = 'onBlur',
    onFieldChange = 'onChange',
    email,
  } = props;

  const classes = classNames(className, 'form-control', {
    'is-invalid': errors[name] && touched[name],
  });

  const handleInputChange = (event: any) => {
    onChange && onChange(event);
    handleOnChange && handleOnChange(event);
  };

  const handleInputBlur = (event: any) => {
    onBlur && onBlur(event);
    handleOnBlur && handleOnBlur(event);
  };

  const clone = (obj: ReactElement): any => {
    const {
      props: { id, type },
    } = obj;
    const childProp = Object.keys(props).reduce((ret, key) => {
      const exist = typeof obj.props[key] !== 'undefined';

      //console.log(ReactDOM.findDOMNode(props));
      //console.log(typeof obj.props);

      /*if (!isObject(props[key])) {
        if (!exist) {
          console.log(key);
          console.log(props);
        }
      }*/

      return isObject(props[key])
        ? ret
        : { ...ret, ...(nonProps.indexOf(key) === -1 && { [key]: props[key] }) };
    }, {});

    return id === name
      ? React.cloneElement(obj, {
          ...childProp,
          name,
          value,
          className: classes,
          [onFieldBlur]: handleInputBlur,
          [onFieldChange]: handleInputChange,
          ...(email && { type: 'email' }) || {type},
          key: name,
        })
      : obj;
  };

  return (
    <div className="form-group">
      {label && <label htmlFor={name}>{required ? requiredFormatter(label) : label}</label>}
      {getChildrens(children, clone)}
      {touched[name] && errors[name] && (
        <ErrorMessage name={name} component="div" className="invalid-feedback" />
      )}
    </div>
  );
};

export default connect(FieldContent);
