import { Dispatch } from 'redux';
import AuthService, { LoginRequest } from '../../services/AuthService';
import { showError } from '../global/action';
import { SessionActions } from './type';

export const loginAction = (loginData: LoginRequest, callback?: () => void) => async (
  dispatch: Dispatch
) => {
  try {
    dispatch({ type: SessionActions.LOADING });
    const loginResult = await AuthService.login(loginData);
    dispatch({ type: SessionActions.LOGIN_SUCCESS, payload: loginResult });
    callback && callback();
  } catch (e) {
    dispatch({ type: SessionActions.LOGIN_FAILURE });
    dispatch(showError('errorLogin'));
  }
};

export const logoutAction = () => async (dispatch: Dispatch) => {
  try {
    dispatch({ type: SessionActions.LOADING });
    await AuthService.logout();
    dispatch({ type: SessionActions.LOGOUT });
    return true;
  } catch (e) {
    // If something bad happens while trying to do a BE logout, we just ignore and logout locally
    dispatch({ type: SessionActions.LOGOUT });
  }
};
