import API from '../services/ApiService';
import { endpoints } from '../config/AppUrls';


interface InfoProps  {
  data: object; 
  action: string;
  method: string;
}

export class serviceIntegrity {
  static send = async (info: InfoProps) => {
    let response = {}
    const { data , action, method } = info
    const endpoint = endpoints[action];
    
    switch(method) {
      case 'GET':
        response = await API.get(endpoint, data);
        break;
      case 'DELETE':
          response = await API.get(endpoint, data);
          break;  
      default:
        response = await API.post(endpoint, data);
    }

    if (!response) {
      throw new Error('Service error...');
    }
    return response;
  };

}
