import { handleActions } from 'redux-actions';
import { MenuActions } from './type';
import { ActionType } from 'typesafe-actions';

export const initialState = {
  selected: '',
  blink: '',
};

export type MenuState = typeof initialState;

const MenuReducer = handleActions<MenuState, ActionType<any>>(
  {
    [MenuActions.MENU_SELECTED]: (state, {selected }:any ) => {
      return { ...state, selected  };
    },
    [MenuActions.MENU_BLINK]: (state, {blink }:any ) => {
      return { ...state, blink  };
    },
  },
  initialState
);

export default MenuReducer;
