import React from 'react';
import { RootState } from 'ReduxTypes';
import { isLoading } from '../../../redux/global/selector';
import { connect } from 'react-redux';

import './Loading.scss';

interface ILoadingProps {
  withOverlay?: boolean;
  isLoading: boolean;
}
const Loading = ({ withOverlay = true, isLoading }: ILoadingProps) => {
  return isLoading ? (
    <div className="loading-wrapper">
      <div className="loading-wrapper_spinner" />
      {withOverlay && <div className="loading-wrapper_overlay" />}
    </div>
  ) : null;
};

const mapStateToProps = (state: RootState) => {
  return {
    isLoading: isLoading(state),
  };
};

export default connect(mapStateToProps)(Loading);
