export const jsonFormatter = (json: string) => {
  return JSON.stringify(JSON.parse(json), null, 2);
};

export const isValidJson = (json: string) => {
  try {
    JSON.parse(json);
  } catch (e) {
    return false;
  }
  return true;
};

export const getDecimalSeparator = (locale: any) => {
  const numberWithDecimalSeparator = 1.1;

  const separator = numberWithDecimalSeparator.toLocaleString(locale).substring(1, 2);
  return checkCorrectSeparator(separator) ? separator : '.';
};

export const fileNameFormatter = (fileName: string) => {
  const splitName = fileName.split('.');
  const name = splitName[0];
  const ext = splitName.length > 1 ? `.${splitName[1]}` : '';
  const formattedName =
    name.length > 24
      ? `${name.substring(0, 19)}...${name.substring(name.length - 2, name.length)}${ext}`
      : `${name}${ext}`;
  return formattedName;
};

export const getThousandSeparator = (locale: any) => {
  const numberWithThousandSeparator = 10000;

  const separator = numberWithThousandSeparator.toLocaleString(locale).substring(2, 3);
  return checkCorrectSeparator(separator) ? separator : ',';
};

const checkCorrectSeparator = (separatorToCheck: any) => {
  return separatorToCheck.length === 1 && (separatorToCheck === '.' || separatorToCheck === ',');
};

export const classExist = (classNames: string, className: string) => {
  const allClasses = classNames.split(' ');
  return allClasses.indexOf(className) !== -1;
};
