import { Dispatch } from 'redux';
import { showError } from '../redux/global/action';
import { serviceIntegrity } from './service';

interface SubmitActionProps {
  data: object;
  action: string;
  method: string;
  msgError: string;
}

export const submitAction = (info: SubmitActionProps, callback?: () => void) => async (
  dispatch: Dispatch
) => {
  const { action, msgError = '' } = info;

  try {
    dispatch({ type: action });
    const serviceResult = await serviceIntegrity.send(info);
    dispatch({ type: action + '_SUCCESS', payload: serviceResult });
    callback && callback();
  } catch (e) {
    dispatch({ type: action + '_FAILURE' });
    dispatch(showError(msgError));
  }
};
