import React, { useState, useEffect, useRef } from 'react';
import classNames from 'classnames';
import { DottedTree, Icon } from '../../widgets/';
import './Panel.scss';
import { IMenu } from '../../../config/Menu';
import {
  IntegrityConnect,
  getMenuSelected,
  selectMenuAction,
  setBlinkMenuAction,
} from '../../../integrity/connect';
import { SubMenu } from '../../widgets';
import { bool } from 'yup';
import Scrollbars from 'react-custom-scrollbars';
import { classExist } from '../../../helpers/Utils';

interface IPanelProps {
  getMenuSelected: typeof getMenuSelected;
  selectMenu: typeof selectMenuAction;
  setBlinkMenu: typeof setBlinkMenuAction;
  children?: any;
  className?: string;
  header?: any;
  footer?: any;
  icon?: string;
  iconRight?: any;
  submenu?: IMenu[];
  rotateIcon?: any;
  isDropDown?: boolean;
  onClick?: () => void;
  withDottedTree?: boolean;
  childrensWithDottedTree?: boolean;
  name?: string;
  menuExpanded?: boolean;
  dropDownContainer?: any;
  withScroll?: boolean;
}
const Panel = ({
  children,
  className = '',
  header,
  footer,
  icon,
  iconRight,
  submenu,
  rotateIcon,
  isDropDown = false,
  onClick,
  withDottedTree = false,
  childrensWithDottedTree = false,
  name = '',
  getMenuSelected,
  selectMenu,
  setBlinkMenu,
  menuExpanded = false,
  dropDownContainer = null,
  withScroll = false,
}: IPanelProps) => {
  const [submenuHover, setSubmenuHover] = useState(-1);
  const [showMenu, setShowMenu] = useState(menuExpanded);

  const key = btoa(name || header);
  const inputEl: any = useRef(null);

  const submenuSelected = submenu
    ? submenu.reduce((ret: any, { text }: any, I: number) => {
        const key = btoa(`${name}_${text}`);
        if (key === getMenuSelected.toString()) return I;
        return ret;
      }, null)
    : null;

  const handleClick = () => {
    if (submenu) {
      if (showMenu && submenuSelected !== null) {
        setBlinkMenu(getMenuSelected.toString());
        return;
      }
      setShowMenu(!showMenu);
    } else if (!isDropDown) {
      if (classExist(className, 'main-menu')) {
        handleClickSubmenu(key, onClick);
      }
    }
    onClick && onClick();
  };

  const handleClickOutside = (e: any) => {
    isDropDown &&
      setTimeout(() => {
        if (inputEl.current && !inputEl.current.contains(e.target)) {
          setShowMenu(false);
        }
      }, 200);
  };

  useEffect(() => {
    isDropDown && document.addEventListener('mousedown', handleClickOutside);
  });

  const handleClickSubmenu = (key: string, eventClick?: () => void) => {
    console.log(isDropDown);
    if (classExist(className, 'item-menu') && !isDropDown) selectMenu(key);
    eventClick && eventClick();
  };

  return (
    <>
      <div
        ref={inputEl}
        className={classNames('panel', className, {
          'submenu-selected': getMenuSelected.toString() === key,
          'menu-open': showMenu,
          'menu-selected': submenuSelected !== null,
          'menu-over': submenuHover !== -1,
        })}
        onClick={handleClick}
      >
        {withDottedTree && <DottedTree />}
        {icon && (
          <div>
            <div
              className={classNames(
                {
                  'is-rotate': showMenu && rotateIcon === 'iconLeft',
                },
                'icon-menu icon-left'
              )}
            >
              <Icon name={icon} />
            </div>
          </div>
        )}
        {typeof header === 'string' ? <div className="panel-header">{header}</div> : header}
        {children}
        {footer && <div className="panel-footer">{footer}</div>}
        {iconRight && (
          <div
            className={classNames(
              {
                'is-rotate': showMenu && rotateIcon === 'iconRight',
              },
              'icon-menu icon-right'
            )}
          >
            <Icon name={iconRight} />
          </div>
        )}
      </div>
      {(submenu || dropDownContainer) && (
        <div className={classNames('panel-submenu', { hide: !showMenu, 'drop-down': isDropDown })}>
          {dropDownContainer}
          {submenu && (
            <SubMenu
              submenuSelected={submenuSelected}
              submenu={submenu}
              handleClickSubmenu={handleClickSubmenu}
              childrensWithDottedTree={childrensWithDottedTree}
              name={name}
            />
          )}
        </div>
      )}
    </>
  );
};

export default IntegrityConnect(Panel, ['getMenuSelected', 'selectMenu', 'setBlinkMenu']);
