import * as React from 'react';
import './ModalContainer.scss';
import { EModalComponentType, TModalState } from '../../redux/global/type';
import classNames from 'classnames';
import ConfirmationModal from './Modal/ConfirmModal/ConfirmationModal';
import Modal from './Modal/Modal';

interface ModalContainerProps {
  modal: TModalState;
  hideModal: () => void;
}

interface ModalContainerState {
  modal: TModalState;
  isHiding?: boolean;
}

class ModalContainer extends React.Component<ModalContainerProps, ModalContainerState> {
  state = {
    modal: this.props.modal,
    isHiding: false,
  };

  componentDidUpdate(prevProps: ModalContainerProps) {
    const { modal: prevModal } = prevProps;
    const { modal } = this.props;

    if (!modal.show && prevModal.show) {
      document.body.style.overflow = 'auto';
      this.setState({ isHiding: true });
      setTimeout(() => this.setState({ modal, isHiding: false }), 340);
    } else if (modal.show !== prevModal.show) {
      document.body.style.overflow = 'hidden';
      this.setState({ modal });
    }
  }

  render() {
    const { hideModal } = this.props;
    const { modal, isHiding } = this.state;
    const {
      type,
      show,
      options: { className = '', ...options },
    } = modal;
    const closeModal = () => {
      const { backDrop = false } = options;
      if (backDrop) {
        hideModal();
      }
    };

    const componentByType: (t: EModalComponentType) => React.ReactNode = ComponentType => {
      switch (ComponentType) {
        case EModalComponentType.Confirmation:
          return ConfirmationModal;

        case EModalComponentType.Modal:
        default:
          return Modal;
      }
    };

    if (!show) {
      return null;
    }
    const ModalComponent: any = componentByType(type);
    const classNameGlobal = classNames('global-modal', { hiding: isHiding });
    const classNameModal = classNames(className, {
      hiding: isHiding,
      'show normal': show && !isHiding,
    });
    return (
      <div className={classNameGlobal}>
        <div className="global-modal__overlay" onClick={closeModal} />
        <div className="global-modal__content">
          <ModalComponent onClose={hideModal} {...options} className={classNameModal} />
        </div>
      </div>
    );
  }
}

export default ModalContainer;
