import { RootState } from 'ReduxTypes';
import { GlobalState } from './reducer';
import { createSelector } from 'reselect';

const getGlobal: (s: RootState) => GlobalState = state => state.global;

export const getModal = createSelector(getGlobal, (state: GlobalState) => state.modal);
export const getToasts = createSelector(getGlobal, (state: GlobalState) => state.toasts);
export const isLoading = createSelector(getGlobal, (state: GlobalState) => state.isLoading);
export const isLaunched = createSelector(getGlobal, (state: GlobalState) => state.isLaunched);
export const getFetchs = createSelector(getGlobal, (state: GlobalState) => state.fetchs);
