import { IGenericFetch } from './type';
import { TModalState, EToastType } from './type';
import { Dispatch } from 'redux';
import { ApiError } from '../../services/ApiService';
import { logoutStatus } from '../../config/AppConstants';
import { logoutAction } from '../session/action';
import { GlobalService } from '../../services/GlobalService';

export enum GlobalActions {
  // global state
  LOADING = 'global/LOADING',

  // request
  REQUEST_START = 'global/REQUEST_START',
  REQUEST_SUCCESS = 'global/REQUEST_SUCCESS',
  REQUEST_FAILED = 'global/REQUEST_FAILED',

  // modal
  SHOW_MODAL = 'modal/SHOW_MODAL',
  HIDE_MODAL = 'modal/HIDE_MODAL',

  // toast
  SHOW_TOAST = 'toast/SHOW_TOAST',
  REMOVE_TOAST = 'toast/REMOVE_TOAST',

  // fetch

  FETCH_ADD = 'fetch/FETCH_ADD',
  //FETCH_ADD = 'fetch/FETCH_ADD',

  // global state
  GLOBAL_STATE = 'global/STATE',
}

/*export const requestStartAction = createAction(GlobalActions.REQUEST_START);
export const requestSuccessAction = createAction(GlobalActions.REQUEST_SUCCESS);
export const requestFailedAction = createAction(GlobalActions.REQUEST_FAILED);*/

export const showModalAction = (modal: TModalState) => {
  return {
    type: GlobalActions.SHOW_MODAL,
    payload: { modal },
  };
};

export const hideModalAction = () => {
  return {
    type: GlobalActions.HIDE_MODAL,
  };
};

export const showToastAction = (type: EToastType, message: string) => {
  return {
    type: GlobalActions.SHOW_TOAST,
    payload: { type, message },
  };
};

export const removeToastAction = (id: number) => {
  return {
    type: GlobalActions.REMOVE_TOAST,
    payload: { id },
  };
};

export interface IServiceHelper {
  service: () => any;
  successText?: string;
  actionName?: string;
  withLoading?: boolean;
  successCallback?: (response: any) => void;
  errorCallback?: () => void;
}

export const serviceHelperAction = (serviceHelper: IServiceHelper) => async (
  dispatch: Dispatch<any>
) => {
  const {
    withLoading,
    service,
    successText,
    successCallback,
    errorCallback,
    actionName,
  } = serviceHelper;
  try {
    withLoading &&
      dispatch({
        type: GlobalActions.REQUEST_START,
      });
    const payload = await service();
    actionName &&
      dispatch({
        type: actionName,
        payload,
      });
    withLoading &&
      dispatch({
        type: GlobalActions.REQUEST_SUCCESS,
      });
    successText && dispatch(showSuccess(successText));
    successCallback && successCallback(payload);
  } catch (error) {
    dispatch(handleErrorAction(error));
    errorCallback && errorCallback();
  }
};

export const showError = (message: string = 'errorNetworkGeneral') => {
  return showToastAction(EToastType.Danger as EToastType, message);
};

export const showSuccess = (message: string = 'successActionGeneral') => {
  return showToastAction(EToastType.Success as EToastType, message);
};

export const showWarning = (message: string = 'warningActionGeneral') => {
  return showToastAction(EToastType.Warning as EToastType, message);
};

export const handleErrorAction = (error: ApiError, showToast: boolean = true) => (
  dispatch: Dispatch<any>
) => {
  dispatch({ type: GlobalActions.REQUEST_FAILED });
  if (logoutStatus.includes(error.status)) {
    dispatch(logoutAction());
    dispatch(showError('errorSessionExpired'));
  } else if (showToast) {
    dispatch(showError(error.description || 'errorNetworkGeneral'));
  }
};

export const fetchAddAction = (payload: IGenericFetch) => {
  return {
    type: GlobalActions.FETCH_ADD,
    payload,
  };
};

export const fetchAdd = (path: string, query = {}) => {
  return fetchAddAction({
    [new Date().getTime()]: {
      path,
      query,
    },
  });
};

export const fetchCommit = (json: any) => async (dispatch: Dispatch<any>, getState: any) => {
  const {
    global: { fetchs },
  } = getState();

  await fectchData(dispatch, json, fetchs);
  setGlobalState(dispatch, { fetchs: [] });
};

export const _fectchData = async (dispatch: Dispatch, action: string, data: {}) => {
  try {
    dispatch({ type: GlobalActions.REQUEST_START });
    const result = await GlobalService.fetch(data);
    dispatch({ type: GlobalActions.REQUEST_SUCCESS });
    dispatch({ type: `${action}_SUCCESS`, payload: result });
  } catch (e) {
    dispatch({ type: GlobalActions.REQUEST_FAILED });
    dispatch({ type: `${action}_FAILURE` });
    dispatch(showError(e.description || 'errorNetworkGeneral'));
  }
};

export const fectchData = async (dispatch: Dispatch, action: string, data: {}) => {
  try {
    dispatch({ type: GlobalActions.REQUEST_START });
    const result = await GlobalService.fetch({ data, action: action.toLowerCase() });
    dispatch({ type: GlobalActions.REQUEST_SUCCESS });
    dispatch({ type: `${action}_SUCCESS`, payload: result });
    setGlobalState(dispatch, result);
  } catch (e) {
    dispatch({ type: GlobalActions.REQUEST_FAILED });
    dispatch({ type: `${action}_FAILURE` });
    dispatch(showError(e.description || 'errorNetworkGeneral'));
  }
};

export const setGlobalStateAction = (payload: {}) => {
  return {
    type: GlobalActions.GLOBAL_STATE,
    payload,
  };
};

export const setGlobalState = (dispatch: Dispatch, data = {}) => {
  return dispatch(setGlobalStateAction(data));
};
