import { Dispatch } from 'redux';
//import { showError } from '../global/action';
import { StorageHelper } from '../../helpers/StorageHelper';
import { GlobalActions, showError, showSuccess, hideModalAction } from './../global/action';
import { SessionActions } from './../session/type';

const storage = new StorageHelper();

export const mockPostAction = (data: any, callback?: () => void) => async (dispatch: Dispatch) => {
  dispatch({ type: GlobalActions.REQUEST_START });

  await new Promise(resolve => {
    setTimeout(resolve, 300);
  });

  const { name, id, password } = data;

  const BD = storage.get('madory-database');
  const { users } = BD;
  let user = {};

  switch (data.action) {
    case 'logout':
      dispatch({ type: SessionActions.LOGOUT });
      dispatch({ type: GlobalActions.REQUEST_SUCCESS });
      break;
    case 'login':
      const fndUser = users.filter((I: any) => I.username === data.username);

      if (fndUser.length) {
        const user = fndUser[0];
        if (user.password === btoa(data.password)) {
          dispatch({ type: SessionActions.LOGIN_SUCCESS, payload: { user } });
          dispatch({ type: GlobalActions.REQUEST_SUCCESS });
          return true;
        }
      }

      dispatch({ type: GlobalActions.REQUEST_FAILED });
      dispatch(showError('errorLoginError'));
      return false;

    case 'change':
      BD.users = BD.users.map((i: any) => {
        if (i.id === id) {
          i.password = btoa(password);
          user = i;
        }
        return i;
      });

      storage.add('madory-database', BD);

      dispatch({ type: GlobalActions.REQUEST_SUCCESS });
      dispatch(showSuccess('succChangePass'));
      dispatch(hideModalAction());
      return false;

    case 'modify':
      BD.users = BD.users.map((i: any) => {
        if (i.id === id) {
          i.name = name;
          user = i;
        }
        return i;
      });

      storage.add('madory-database', BD);
      dispatch({ type: SessionActions.LOGIN_SUCCESS, payload: { user } });
      dispatch({ type: GlobalActions.REQUEST_SUCCESS });
      dispatch(showSuccess('succChangeName'));
      dispatch(hideModalAction());
      return false;

    case 'reset':
      BD.users = BD.users.map((i: any) => {
        if (i.id === id) {
          i.password = btoa('123');
          user = i;
        }
        return i;
      });

      storage.add('madory-database', BD);

      dispatch({ type: GlobalActions.REQUEST_SUCCESS });
      dispatch(showSuccess('succResetPass'));
      dispatch(hideModalAction());
      return false;
  }
};

export const start = (force = false) => {
  const madoryDatabase = {
    users: [
      {
        id: '1',
        username: 'admin@test.com',
        name: 'Main Admin',
        password: btoa('admin'),
        language: 'en',
        role: 'admin',
      },
      {
        id: '2',
        username: 'customer@test.com',
        name: 'Eddie McLawrens',
        password: btoa('customer'),
        language: 'en',
        role: 'customer',
      },
    ],
  };

  const existData = storage.get('madory-database');

  if (force || !existData) {
    alert(1);
    storage.add('madory-database', madoryDatabase);
  }
};
