import { IGenericObject } from '../types/global';

const resources = {
  logos: 'https://resources.customer-panel.com/misc/logo/',
  flags: 'https://resources.customer-panel.com/country/flag/',
  paymentMethods: 'https://resources.customer-panel.com/cashin/payment_method/square/',
};

const endpoints: any = {
  baseUrl: process.env.REACT_APP_API_BASE_URL,
  // Authentication
  login: '/auth/login',
  logout: '/auth/destroy',
  // Filters
  action: 'ajax/{action}.json',
};

const Routes = {
  ROOT: '/',
  LOGIN: '/login',
  LOGOUT: '/logout',
  HOME: '/home',
};

export const buildPathWithParams = (path: string, params: any) => {
  let finalPath = path;
  Object.keys(params).forEach(key => {
    finalPath = finalPath.replace(`:${key}`, params[key]);
  });
  return finalPath;
};

const getCountryImage = (countryCode: string) =>
  countryCode
    ? `${resources.flags}${countryCode.toUpperCase()}.svg`
    : `${process.env.PUBLIC_URL}/img/default-image.svg`;

const getPaymentMethodImage = (paymentMethodCode: string) =>
  paymentMethodCode
    ? `${resources.paymentMethods}${paymentMethodCode.toUpperCase()}.svg`
    : `${process.env.PUBLIC_URL}/img/default-image.svg`;

const handleErrorImage = (e: any) => {
  e.target.src = `${process.env.PUBLIC_URL}/img/default-image.svg`;
};

const urlHelpers = {
  getCountryImage,
  getPaymentMethodImage,
  handleErrorImage,
};

export const exportType: IGenericObject = {
  deposit: 'deposit',
  cashouts: 'cashouts',
  candidates: 'deposits/releaseCandidates',
};

export { resources, endpoints, urlHelpers, Routes };
