import moment, { Moment } from 'moment';
import { locale } from '../config/i18n';
import { candidatesDaysSpan } from '../config/AppConstants';

interface ICandidatesRange {
  from: string;
  to: string;
}

const convertSecondsToTime = (seconds: string) => {
  const parsed = parseInt(seconds, 10);
  return moment.duration(parsed, 'seconds');
};

const DateFormats = {
  RegularDate: 'L',
  RegularDateTime: 'L H:mm:ss',
  TextDateTime: 'll H:mm:ss',
  TextDate: 'll',
  MonthAndYear: 'MM/YYYY',
};

const formatDateUTCAs = (dateObject: string, dateFormat: string) => {
  return moment.utc(dateObject).local().format(dateFormat);
};

const formatDateAs = (dateObject: Date, dateFormat: string) => {
  return moment(dateObject).format(dateFormat);
};

const formatDateFromNow = (dateObject: Date) => {
  return moment.utc(dateObject).fromNow();
};

const candidatesDateRange = (creationDate: Date | Moment): ICandidatesRange => {
  const from = moment(creationDate).subtract(30, 'minutes').format('X');
  let to = moment(creationDate).add(candidatesDaysSpan, 'days');
  if (to.isAfter(moment())) {
    to = moment().endOf('day');
  }
  return { from, to: to.format('X') };
};

moment.locale(locale);

export {
  convertSecondsToTime,
  formatDateAs,
  formatDateUTCAs,
  formatDateFromNow,
  DateFormats,
  candidatesDateRange,
};
