import React from 'react';
import { FiChevronRight } from 'react-icons/fi';
import { AiOutlineUserAdd, AiOutlineLock, AiOutlineUnlock, AiOutlineLogout, AiOutlineSetting, AiOutlineHome, AiOutlineDashboard, AiOutlineDesktop, AiOutlineAppstore, AiOutlineBank, } from 'react-icons/ai';
import './Icon.scss';



interface IIconProps {
  name: string;
  size?: string;
}
const Icon = ({
  name,
  size = '18px'
}: IIconProps) => {
  switch (name) {
    case 'home': return <AiOutlineHome size={size} />
    case 'desktop': return <AiOutlineDesktop size={size} />
    case 'dashboard': return <AiOutlineDashboard size={size} />
    case 'appstore': return <AiOutlineAppstore size={size} />
    case 'bank': return <AiOutlineBank size={size} />
    case 'arrow-right': return <AiOutlineBank size={size} />
    case 'setting': return <AiOutlineSetting size={size} />
    case 'logout': return <AiOutlineLogout size={size} />
    case 'unlock': return <AiOutlineUnlock size={size} />
    case 'lock': return <AiOutlineLock size={size} />
    case 'user-add': return <AiOutlineUserAdd size={size} />


    default: return <FiChevronRight size={size} />
  }
};

export default Icon;
