import React, { ReactElement } from 'react';
import { IntegrityConnect } from '../../../integrity/connect';
import { Formik, Form } from 'formik';
import * as Yup from 'yup';
import './FormikForm.scss';
import { getChildrens } from '../../../integrity/helpers';
import { fetchAdd, fetchCommit } from '../../../redux/global/action';
import { mockPostAction } from '../../../redux/mock/action';

interface FormikFormProps {
  children: ReactElement;
  initialValues?: Object;
  validationSchema: any;
  className: any;
  action: string;
  method?: string;
  onSubmit?(value: any, formProps: Object): any;
  submit: any;
  msgError: string;
  fetchAdd: typeof fetchAdd;
  fetchCommit: typeof fetchCommit;
  mockPost: typeof mockPostAction;
  onSuccess: () => {};
}

const FormikForm = (props: FormikFormProps) => {
  const {
    children,
    validationSchema,
    initialValues = Object.keys(validationSchema.fields).reduce((ret, item) => {
      return {
        ...ret,
        [item]: '',
      };
    }, {}),
    onSubmit,
    className,
    action,
    submit,
    method = 'POST',
    msgError,
    fetchAdd,
    fetchCommit,
    mockPost,
    onSuccess,
  } = props;

  const clone = (obj: any): any => {
    let newProps = {};
    const getTests = (path: string) => {
      try {
        return Yup.reach(validationSchema, path)
          .describe()
          .tests.reduce((ret, item) => {
            return {
              ...ret,
              [item.name]: true,
            };
          }, {});
      } catch (error) {
        return {};
      }
    };

    const {
      props: { name },
    } = obj;
    const { fields } = validationSchema.describe();
    if (fields[name]) newProps = getTests(name);

    return React.cloneElement(obj, {
      ...newProps,
      key: `key_formik_form_${name}`,
    });
  };

  const childrens = children && getChildrens(children, clone);
  const getValues = childrens.reduce(
    (ret: {}, { props: { name, value } = { name: '', value: '' } }: any) => {
      return {
        ...ret,
        ...(value && { [name]: value }),
      };
    },
    {}
  );

  return (
    <Formik
      initialValues={{ ...initialValues, ...getValues }}
      validationSchema={validationSchema}
      onSubmit={async (values, formProps) => {
        const { action } = props;
        const data = onSubmit ? onSubmit(values, formProps) : values;
        if (!data) return;

        (await mockPost({ ...data, action })) && onSuccess();
      }}
    >
      {formikform => {
        return <Form className={className}>{childrens}</Form>;
      }}
    </Formik>
  );
};

export default IntegrityConnect(FormikForm, ['fetchAdd', 'fetchCommit', 'mockPost']);
