import React from 'react';
import './Login.scss';
import { loginAction } from '../../redux/session/action';
import { showToastAction } from '../../redux/global/action';
import * as Yup from 'yup';
import { Field } from 'formik';
import FormikForm from '../../components/formik/FormikForm/FormikForm';
import { TextInput } from '../../components/formik/fields';
import { Button, EButtonType } from '../../components/commons';
import { RouteComponentProps } from 'react-router-dom';
import { Routes } from '../../config/AppUrls';
import { t } from '../../config/i18n';

interface ILoginPageProps extends RouteComponentProps {
  login: typeof loginAction;
  showToast: typeof showToastAction;
  loading: boolean;
}

const LoginPage = (props: ILoginPageProps) => {
  const { history, loading } = props;
  const redirectHome = () => history.push(Routes.HOME);

  const loginValidation = Yup.object().shape({
    username: Yup.string().email(t('validationEmail')).required(t('validationRequired')),
    password: Yup.string().required(t('validationRequired')),
  });

  return (
    <div className="login-page">
      <div className="login-page__container">
        <div className="d-md-flex h-md-100 align-items-center">
          <div className="logoarea text-white h-100 ">
            <div className="p-5">
              <h5>{t('appTitle')}</h5>
              <br></br>
              <div className="align-left">{t('appWelcome')}</div>
            </div>
          </div>
          <div className="col-md-6 p-3 bg-white h-md-100 loginarea">
            <div className="p-5 justify-content-center">
              <h3>{t('loginTitle')}</h3>

              <FormikForm
                action="login"
                validationSchema={loginValidation}
                className="login-page__container__form"
                msgError="Login Failed..."
                onSuccess={() => {
                  redirectHome();
                }}
              >
                <Field
                  component={TextInput}
                  label={t('labelEmail')}
                  name="username"
                  value="admin@test.com"
                  placeholder={t('placeholderEmail')}
                />
                <Field
                  component={TextInput}
                  label={t('labelPassword')}
                  name="password"
                  type="password"
                  value="admin"
                  placeholder={t('placeholderPassword')}
                  showAndHide={true}
                />
                <Button
                  buttonType={EButtonType.primary}
                  block
                  type="submit"
                  className="text-uppercase"
                  disabled={loading}
                  loading={loading}
                >
                  {t('buttonLogIn')}
                </Button>
              </FormikForm>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LoginPage;
