import API from './ApiService';
import { endpoints } from '../config/AppUrls';
import { User } from '../redux/session/type';

export interface LoginRequest {
  username: string;
  password: string;
}

export class AuthService {
  static login = async (loginData: LoginRequest) => {
    const loginResult: User = await API.post(endpoints.login, loginData);
    if (!loginResult) {
      throw new Error('Invalid Credentials');
    }
    return loginResult;
  };

  static logout = async () => {
    const logoutResult = await API.delete(endpoints.logout);
    if (!logoutResult) {
      throw new Error('Invalid Credentials');
    }
    return logoutResult;
  };
}

export default AuthService;
