import React, { useEffect } from 'react';
import Dashboard from '../../pages/Dashboard/Dashboard';
import './Home.scss';
import { Welcome } from '../../pages/Templates';
import { showError } from '../../redux/global/action';
import { RouteComponentProps, withRouter } from 'react-router';
import { IntegrityConnect } from '../../integrity/connect';
import { getUser } from '../../redux/session/selector';

interface RouteProps {
  deniedMsg?: string;
}
interface IHomePageProps extends RouteComponentProps<any, any, RouteProps> {
  showError: typeof showError;
  getUser: typeof getUser;
}

const HomePage = (props: IHomePageProps) => {
  const { getUser } = props;

  const { name } = getUser;

  useEffect(() => {
    const {
      location: { state: { deniedMsg } = { deniedMsg: false } },
      showError,
      history,
    } = props;
    if (deniedMsg) {
      showError('errorAccessDenied');
      history.replace({
        state: {},
      });
    }
  }, [props]);
  return (
    <Dashboard user={getUser}>
      <div className="home">
        <Welcome name={name} />
      </div>
    </Dashboard>
  );
};

const router = withRouter(HomePage);
export default IntegrityConnect(router, ['getUser', 'showError']);
