import React, { useState } from 'react';
import './SubMenu.scss';
import { t } from '../../../config/i18n';
import { FiDisc } from 'react-icons/fi';
import classNames from 'classnames';
import { IMenu } from '../../../config/Menu';
import { Panel } from '../../commons';
import { IntegrityConnect, getBlinkMenu } from '../../../integrity/connect';

interface ISubMenuProps {
  submenu: IMenu[];
  setSubmenuHover?: (I: number) => void;
  handleClickSubmenu: (key: string, event: () => void) => void;
  getBlinkMenu: typeof getBlinkMenu;
  submenuSelected: number;
  submenuHover: number;
  childrensWithDottedTree?: boolean;
  name?: string;
}

const SubMenu = ({
  submenu,
  getBlinkMenu,
  handleClickSubmenu,
  submenuSelected,
  childrensWithDottedTree = false,
  name = '',
}: ISubMenuProps) => {
  const [submenuHover, setSubmenuHover] = useState(-1);

  const onMouseMove = (I: number) => {
    setSubmenuHover(I);
  };

  const onMouseLeave = (I: number) => {
    setSubmenuHover(I);
  };

  return (
    <ul>
      {submenu.map(({ icon, text, clickEvent, menuSepar }: any, I: number) => {
        const key = btoa(`${name}_${text}`);
        return (
          <li key={key} onMouseMove={() => onMouseMove(I)} onMouseLeave={() => onMouseMove(-1)}>
            <Panel
              icon={icon}
              className={classNames('item-menu', {
                'blink-panel': key === getBlinkMenu.toString(),
                'submenu-selected': submenuSelected === I,
                'path-selected': I < submenuHover || I < submenuSelected,
                'menu-separ': menuSepar,
              })}
              onClick={() => handleClickSubmenu(key, clickEvent)}
              withDottedTree={childrensWithDottedTree}
            >
              {t(text)}
            </Panel>
          </li>
        );
      })}
    </ul>
  );
};

export default IntegrityConnect(SubMenu, ['getBlinkMenu']);
