import { EModalComponentType, IModal, IToast, IGenericFetch } from './type';
import { ActionType } from 'typesafe-actions';
import { GlobalActions } from './action';
import { handleActions } from 'redux-actions';

const initModal: IModal = {
  show: false,
  type: EModalComponentType.Modal,
  options: {
    title: '',
    backDrop: false,
    children: null,
    onClose: () => {},
  },
};

export interface GlobalState {
  isLoading: boolean;
  isLaunched: boolean;
  modal: IModal;
  toasts: IToast[];
  fetchs: IGenericFetch[];
}

export const initialState: GlobalState = {
  isLoading: false,
  isLaunched: false,
  modal: initModal,
  toasts: [],
  fetchs: [],
};

export default handleActions<GlobalState, ActionType<any>>(
  {
    [GlobalActions.REQUEST_START]: state => {
      return { ...state, isLoading: true };
    },
    [GlobalActions.REQUEST_SUCCESS]: state => {
      return { ...state, isLaunched: true, isLoading: false };
    },
    [GlobalActions.REQUEST_FAILED]: state => {
      return { ...state, isLaunched: true, isLoading: false };
    },
    [GlobalActions.SHOW_MODAL]: (state, action) => {
      return { ...state, modal: { ...action.payload.modal, show: true } };
    },
    [GlobalActions.HIDE_MODAL]: state => {
      return { ...state, modal: { ...initModal } };
    },
    [GlobalActions.SHOW_TOAST]: (state, action) => {
      return { ...state, toasts: [{ ...action.payload, id: state.toasts.length }] };
    },
    [GlobalActions.REMOVE_TOAST]: (state, action) => {
      return { ...state, toasts: state.toasts.filter(toast => toast.id !== action.payload.id) };
    },
    [GlobalActions.FETCH_ADD]: (state, { payload }) => {
      return { ...state, fetchs: [...state.fetchs, payload] };
    },
    [GlobalActions.GLOBAL_STATE]: (state, { payload }) => {
      //console.log(payload);
      return { ...state, ...payload };
    },
  },
  initialState
);
