import React from 'react';
import classNames from 'classnames';
import { ErrorMessage } from 'formik';
import NumberFormat from 'react-number-format';
import { compose } from 'redux';
import { getDecimalSeparator, getThousandSeparator } from '../../../helpers/Utils';
import { INumberInputProps } from './types';
import { injectIntl } from 'react-intl';
import { requiredFormatter } from '../../../helpers/formatters';

const NumberInput = (props: INumberInputProps) => {
  const {
    field: { name, value, onBlur },
    form: { touched, errors, setFieldValue },
    required,
    intl,
    label,
    placeholder,
    autoComplete = 'off',
    disabled = false,
    handleOnChange,
    addOn,
  } = props;

  const decimalSeparator = getDecimalSeparator(intl.locale);
  const groupSeparator = getThousandSeparator(intl.locale);

  const classes = classNames('form-control', { 'is-invalid': errors[name] && touched[name] });

  const valueChange = (name: string, v: any) => {
    const value = v.floatValue ? v.floatValue : v.value;
    setFieldValue(name, value);
    handleOnChange && handleOnChange(value);
  };

  const input = (
    <NumberFormat
      name={name}
      id={name}
      value={value}
      autoComplete={autoComplete}
      onValueChange={v => {
        valueChange(name, v);
      }}
      thousandSeparator={groupSeparator}
      decimalSeparator={decimalSeparator}
      className={classes}
      placeholder={placeholder}
      disabled={disabled}
      onBlur={onBlur}
    />
  );

  return (
    <div className="form-group">
      {label && <label htmlFor={name}>{required ? requiredFormatter(label) : label}</label>}
      {addOn ? (
        <div className="input-group">
          <div className="input-group-append">
            <span className="input-group-text" id="basic-addon2">
              {addOn}
            </span>
          </div>
          {input}
        </div>
      ) : (
        input
      )}
      {touched[name] && errors[name] && (
        <ErrorMessage name={name} component="div" className="invalid-feedback" />
      )}
    </div>
  );
};

export default compose(injectIntl)(NumberInput);
