import React from 'react';
import { removeToastAction } from '../../redux/global/action';
import Toast from './Toast';
import { RootState } from 'ReduxTypes';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { IToast } from '../../redux/global/type';
import { getToasts } from '../../redux/global/selector';

interface IGlobalToastContainerProps {
  toasts: IToast[];
  removeToast: typeof removeToastAction;
}

const GlobalToastContainer = ({ toasts, removeToast }: IGlobalToastContainerProps) => {
  const onClose = (id: number) => {
    removeToast(id);
  };
  return (
    <div className="global-toast-container">
      {toasts.map(toast => (
        <Toast
          key={toast.id}
          message={toast.message}
          type={toast.type}
          onClose={() => onClose(toast.id)}
        />
      ))}
    </div>
  );
};

const mapStateToProps = (state: RootState) => ({
  toasts: getToasts(state),
});

const mapDispatchToProps = (dispatch: any) =>
  bindActionCreators(
    {
      removeToast: removeToastAction,
    },
    dispatch
  );

export default connect(mapStateToProps, mapDispatchToProps)(GlobalToastContainer);
