import React from 'react';
import mainmenu from '../../config/Menu';
import './MainMenu.scss';
import { t } from '../../config/i18n';
import { Panel } from '../commons';

interface IMainMenuProps {
  title?: string;
}
const MainMenu = ({ title }: IMainMenuProps) => (
  <Panel className="left-menu">
    {title && <Panel className="header" header={title} />}
    <Panel className="menu-content" withScroll>
      {mainmenu.map(({ icon, text, submenu }) => (
        <Panel
          icon={icon}
          key={btoa(text)}
          name={btoa(text)}
          className="main-menu item-menu"
          header={t(text)}
          submenu={submenu}
          iconRight={submenu || null}
          rotateIcon="iconRight"
          childrensWithDottedTree
          menuExpanded
        />
      ))}
    </Panel>
  </Panel>
);

export default MainMenu;
