import { ActionType } from 'typesafe-actions';
import * as globalActions from './action';
import * as React from 'react';
import { RootAction } from 'ReduxTypes';
import { IGenericObject } from '../../types/global';

export type GlobalActions = ActionType<typeof globalActions>;

export enum EModalComponentType {
  Modal = 'modal',
  Confirmation = 'confirmation',
}

export enum EModalSize {
  Small = 'small',
  Medium = 'medium',
  Large = 'large',
}

export enum EModalType {
  Default = 'default',
  Warning = 'warning',
  Danger = 'danger',
  Info = 'info',
  Success = 'success',
}

export const modalIconByType: IGenericObject = {
  Default: 'info-circle',
  Warning: 'exclamation-triangle',
  Danger: 'skull-crossbones',
  Info: 'bell',
  Success: 'check',
};

export enum EApproveModalType {
  NotFound = 'NotFound',
  ReleaseCandidate = 'ReleaseCandidate',
  NoDeposit = 'NoDeposit',
  ForReview = 'ForReview',
}

interface IModalCommonProps extends React.PropsWithChildren<{}> {
  onClose?: () => void;
  callback?: () => void;
  backDrop?: boolean;
  className?: string;
}

export interface IModalProps extends IModalCommonProps {
  title: string;
  size?: EModalSize;
  type?: EModalType;
  iconName?: any;
  centered?: boolean;
  defaultClose?: boolean;
  footer?: React.ReactNode;
}

export interface IModalWithConfirm extends IModalProps {
  confirmAction: RootAction;
  showConfirm?: boolean;
  confirmApproveText?: string;
  confirmApproveButtonText?: string;
  confirmCancelButtonText?: string;
}

interface IBaseState {
  show?: boolean;
}

export interface IModalConfirmationProps extends IModalProps {
  confirmMessage: string;
  confirmAction: RootAction;
  confirmBtnText?: string;
  cancelBtnText?: string;
  afterCloseAction?: (data: IGenericObject) => void;
}

export interface IModal extends IBaseState {
  type: EModalComponentType.Modal;
  options: IModalProps;
}

export interface IConfirmationModal extends IBaseState {
  type: EModalComponentType.Confirmation;
  options: IModalConfirmationProps;
}

export type TModalState = IModal | IConfirmationModal;

export enum EToastType {
  Success = 'success',
  Warning = 'warning',
  Danger = 'danger',
  Primary = 'primary',
}

export interface IToast {
  id: number;
  type: EToastType;
  message: string;
  time?: number;
}

export interface IGenericFetchData {
  path: string;
  query: {};
}

export interface IGenericFetch {
  [key: string]: IGenericFetchData;
}
