import { submitAction } from './actions';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';
import { RootState } from 'ReduxTypes';
import {
  fetchAdd,
  fetchCommit,
  setGlobalStateAction,
  showModalAction,
} from '../redux/global/action';

import {
  selectMenuAction,
  setBlinkMenuAction,
} from '../redux/menu/action';

import { isLaunched, isLoading } from '../redux/global/selector';
import { getIsAuthenticated, getUser } from '../redux/session/selector';
import { getMenuSelected, getBlinkMenu } from '../redux/menu/selector';
import { mockPostAction } from '../redux/mock/action';

//export const IntegrityConnect = (component: ComponentType<never>, items: Array<String>): any => {
const IntegrityConnect = (component: any, items: Array<String>): any => {
  const allState: any = (state: RootState) => {
    return {
      isLaunched: isLaunched(state),
      isLoading: isLoading(state),
      isAuthenticated: getIsAuthenticated(state),
      getUser: getUser(state),
      getMenuSelected: getMenuSelected(state),
      getBlinkMenu: getBlinkMenu(state),
    };
  };

  const allDispatch: any = {
    submit: submitAction,
    fetchAdd: fetchAdd,
    fetchCommit: fetchCommit,
    setGlobalState: setGlobalStateAction,
    mockPost: mockPostAction,
    showModal: showModalAction,
    selectMenu: selectMenuAction, 
    setBlinkMenu : setBlinkMenuAction,
  };

  const reduceDispatch = (ret: object, item: any) => {
    return {
      ...ret,
      ...(items.indexOf(item) !== -1 && { [item]: allDispatch[item] }),
    };
  };

  const stateToProps = (state: RootState) => {
    const integrityState = allState(state);
    return Object.keys(integrityState).reduce((ret: object, item: any, obj: any) => {
      return {
        ...ret,
        ...(items.indexOf(item) !== -1 && { [item]: integrityState[item] }),
      };
    }, {});
  };

  const dispatchToProps = Object.keys(allDispatch).reduce(reduceDispatch, {}); // OK

  const mapStateToProps = (state: RootState) => stateToProps(state);

  const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(dispatchToProps, dispatch);

  return connect(mapStateToProps, mapDispatchToProps)(component);
};

export {
  fetchAdd,
  fetchCommit,
  setGlobalStateAction,
  isLaunched,
  isLoading,
  getIsAuthenticated,
  getUser,
  showModalAction,
  IntegrityConnect,
  selectMenuAction,
  getMenuSelected,
  setBlinkMenuAction,
  getBlinkMenu,
};
