import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';
import { RootState } from 'ReduxTypes';
import { loginAction } from '../../redux/session/action';
import LoginPage from './LoginPage';
import { showToastAction } from '../../redux/global/action';
import { withRouter } from 'react-router';
import { isLoading } from '../../redux/global/selector';

const mapStateToProps = (state: RootState) => ({
  loading: isLoading(state),
});

const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      login: loginAction,
      showToast: showToastAction,
    },
    dispatch
  );

const routerLogin = withRouter(LoginPage);

export default connect(mapStateToProps, mapDispatchToProps)(routerLogin);
