import React, { useState } from 'react';
import classNames from 'classnames';
import { FaEye, FaEyeSlash } from 'react-icons/fa';

import { ITextInputProps } from './types';
import { FieldContents } from '.';

const TextInput = (props: ITextInputProps) => {
  const [showPass, setShowPass] = useState(false);

  const {
    showAndHide = false,
    type = 'input',
    field: { name },
  } = props;

  const icon = showPass ? <FaEyeSlash />  : <FaEye />;

  const iconClasses = classNames({
    'input-group-text': true,
    'icon-with-pointer': showPass,
    'rounded-right': true,
  });

  return (
    <FieldContents {...props}>
      {type === 'password' && showAndHide ? (
        <div className="input-group default-input-group">
          <input id={name} type={showPass ? 'input' : 'password'} />
          <div className="input-group-append">
            <span
              className={iconClasses}
              onClick={() => setShowPass(!showPass)}
              onKeyPress={() => setShowPass(!showPass)}
            >
              {icon}
            </span>
          </div>
        </div>
      ) : (
        <input id={name} type={type} />
      )}
    </FieldContents>
  );
};

export default TextInput;
