import { RootState } from 'ReduxTypes';
import { MenuState } from './reducer';
import { createSelector } from 'reselect';

const getMenu: (s: RootState) => MenuState = state => state.menu;

export const getMenuSelected = createSelector(getMenu, (state: MenuState) => state.selected);
export const getBlinkMenu = createSelector(getMenu, (state: MenuState) => state.blink);
