import React from 'react';
import './Dashboard.scss';
import { Panel, Button, EButtonType } from '../../components/commons';
import { t } from '../../config/i18n';
import * as Yup from 'yup';
import { withRouter, RouteComponentProps } from 'react-router';
import { IntegrityConnect, showModalAction, isLoading } from '../../integrity/connect';
import { EModalComponentType, EModalType, EModalSize } from '../../redux/global/type';
import FormikForm from '../../components/formik/FormikForm/FormikForm';
import { Field } from 'formik';
import { TextInput } from '../../components/formik/fields';
import { mockPostAction } from '../../redux/mock/action';
import { FiSettings } from 'react-icons/fi';
import MainMenu from '../../components/MainMenu/MainMenu';

interface IDashboardProps extends RouteComponentProps {
  children?: any;
  user: any;
  showModal: typeof showModalAction;
  isLoading: typeof isLoading;
  mockPost: typeof mockPostAction;
}

const Dashboard = (props: IDashboardProps) => {
  const {
    children,
    user: { name, id },
    showModal,
    isLoading,
    mockPost,
    //history,
  } = props;

  const MainForm = ({ modalType }: any) => {
    const loading = !!isLoading;

    const loginValidation = Yup.object().shape({
      ...(modalType === 'modify' && { name: Yup.string().required(t('validationRequired')) }),
      ...(modalType === 'change' && { password: Yup.string().required(t('validationRequired')) }),
      ...(modalType === 'change' && {
        confirmPassword: Yup.string()
          .required(t('validationRequired'))
          .oneOf([Yup.ref('password'), ''], t('validationPasswordEqual')),
      }),
    });

    return (
      <div>
        <FormikForm
          action={modalType}
          validationSchema={loginValidation}
          className="info-page__container__form"
          msgError="Login Failed..."
          onSuccess={() => {}}
        >
          <Field component={TextInput} name="id" type="hidden" value={id} />
          {modalType === 'modify' && (
            <Field
              component={TextInput}
              label={t('labelUserName')}
              name="name"
              value={name}
              placeholder={t('placeholderUserName')}
            />
          )}
          {modalType === 'change' && (
            <Field
              component={TextInput}
              label={t('labelPassword')}
              name="password"
              type="password"
              placeholder={t('placeholderPassword')}
              showAndHide={true}
            />
          )}
          {modalType === 'change' && (
            <Field
              component={TextInput}
              label={t('labelConfirmPassword')}
              name="confirmPassword"
              type="password"
              placeholder={t('placeholderConfirmPassword')}
              showAndHide={true}
            />
          )}
          <Button
            buttonType={EButtonType.primary}
            block
            type="submit"
            className="text-uppercase"
            disabled={loading}
            loading={loading}
          >
            {t('buttonSubmit')}
          </Button>
        </FormikForm>
      </div>
    );
  };

  const showCustomModal = (modalType: string) => {
    switch (modalType) {
      case 'logout':
        showModal({
          type: EModalComponentType.Confirmation,
          options: {
            title: t('titleLogout'),
            confirmMessage: t('messageLogout'),
            confirmAction: async () => {
              await mockPost({
                action: 'logout',
                id,
              });
            },
            confirmBtnText: t('buttonLogout'),
            cancelBtnText: t('buttonStop'),
          },
        });
        break;
      case 'reset':
        showModal({
          type: EModalComponentType.Confirmation,
          options: {
            title: t('titleResetPassword'),
            confirmMessage: t('messageResetPassword'),
            confirmAction: async () => {
              await mockPost({
                action: 'reset',
                id,
              });
            },
            confirmBtnText: t('buttonResetPassword'),
            cancelBtnText: t('buttonStop'),
          },
        });
        break;
      case 'change':
      case 'modify':
        showModal({
          type: EModalComponentType.Modal,
          options: {
            title: t('modalT_' + modalType),
            type: EModalType.Info,
            children: <MainForm modalType={modalType} />,
            size: EModalSize.Medium,
          },
        });
    }
  };

  return (
    <div className="dashboard-container">
      <MainMenu title="Full Panel UI" />
      <Panel
        className="content"
        header={
          <Panel className="header" header={t('titleHome')}>
            <Panel
              header={name}
              iconRight="setting"
              className="navbar-menu"
              rotateIcon="iconRight"
              isDropDown
              submenu={[
                {
                  text: 'menuResetPassword',
                  icon: 'unlock',
                  clickEvent: () => showCustomModal('reset'),
                },
                {
                  text: 'menuChangePassword',
                  icon: 'lock',
                  clickEvent: () => showCustomModal('change'),
                },
                {
                  text: 'menuModifyAccount',
                  icon: 'user-add',
                  clickEvent: () => showCustomModal('modify'),
                },
                {
                  text: 'menuLogout',
                  icon: 'logout',
                  clickEvent: () => showCustomModal('logout'),
                  menuSepar: true,
                },
              ]}
            />
          </Panel>
        }
        footer={<Panel className="footer">Customer Admin Panel (c) 2020 </Panel>}
      >
        <Panel className="body">{children}</Panel>
      </Panel>
    </div>
  );
};

const router = withRouter(Dashboard);

export default IntegrityConnect(router, ['showModal', 'isLoading', 'mockPost']);
