import API from './ApiService';
import { endpoints } from '../config/AppUrls';

export class GlobalService {
  static fetch = async (data: any) => {
    // console.log(data);

    const url = Object.keys(data).reduce((ret: string, item: any) => {
      const fnd = '{' + item + '}';
      const idx = ret.indexOf(fnd);
      //console.log(idx);
      if (idx !== -1) {
        ret = ret.replace(fnd, data[item]);
      }
      return ret;
    }, endpoints.action);

    //console.log(data.data);
    const result: any = await API.get(url, data.data);
    if (!result) {
      throw new Error('Api response error');
    }
    return result;
  };
}
