import * as React from 'react';
import classNames from 'classnames';
import './Modal.scss';
import { IModalProps, EModalSize, EModalType, modalIconByType } from '../../../redux/global/type';
import { Button, EButtonType } from '../../../components/commons';
import { t } from '../../../config/i18n';
import { FaTimes } from 'react-icons/fa';
import { upperFirst } from 'lodash';

const Modal: React.FC<IModalProps> = props => {
  const {
    title,
    children,
    onClose = () => {},
    iconName = modalIconByType.Default,
    size = EModalSize.Medium,
    type = EModalType.Default,
    defaultClose = false,
    className,
    footer,
    callback,
  } = props;
  const classes = classNames('custom-modal fade', className);
  const dialogClasses = classNames('custom-modal__dialog', `${size}`);
  const headerClass = classNames(`header__${type}`);
  const iconNameType: string = modalIconByType[upperFirst(type)];
  const icon = iconNameType || iconName;
  const handleClose = () => {
    onClose && onClose();
    callback && callback();
  };
  return (
    <div className={classes} role="dialog">
      <div className={dialogClasses}>
        <header className={headerClass}>
          <div className="header__icon">
            <FaTimes />
          </div>
          <h3 className="title">{title}</h3>
          <Button className="header__close" buttonType={EButtonType.light} onClick={handleClose}>
            <FaTimes />
          </Button>
        </header>
        <div className="body">{children}</div>
        {defaultClose && !footer && (
          <footer>
            <Button
              className="default-close"
              buttonType={EButtonType.default}
              onClick={handleClose}
            >
              {t('buttonClose')}
            </Button>
          </footer>
        )}
        {footer && <footer>{footer}</footer>}
      </div>
    </div>
  );
};

export default Modal;
