import React, { ReactElement } from 'react';
import { isArray } from 'util';

/*props pass to Field and exclude to ReactElement in clone */
export const nonProps: any = ['showAndHide', 'email'];

export const getChildrens = (obj: ReactElement, cloneFn: Function): any => {
  if (isArray(obj))
    return obj
      .map((item: any) => {
        return item ? getChildrens(item, cloneFn) : null;
      })
      .filter((e: any) => e);

  //let newObj = cloneFn(obj);
  const {
    props: { children = null },
  } = obj;
  const hasChildrens = children && isArray(children);
  if (hasChildrens) {
    const newProps = {
      children: children
        .map((item: any) => {
          return item ? getChildrens(item, cloneFn) : null;
        })
        .filter((e: any) => e),
    };
    return React.cloneElement(obj, newProps);
  }
  return cloneFn(obj);
};

export const namespace = (types: object, module: string = 'GENERAL', delimiter: string = '_') => {
  const dispatchTypes = ['', '_SUCCESS', '_FAILED'];
  return Object.keys(types).reduce((ret: object, type: any) => {
    return {
      ...ret,
      ...dispatchTypes.reduce((ret: object, v: string) => {
        return {
          ...ret,
          [type + v]: (module + delimiter + type + v).toUpperCase(),
        };
      }, {}),
    };
  }, {});
};
