import * as React from 'react';
import classNames from 'classnames';
import { FaTimes } from 'react-icons/fa';
import './Toast.scss';
import { t } from '../../config/i18n';
import { EToastType } from '../../redux/global/type';

interface IToastProps {
  message: string;
  type: EToastType;
  time: number;
  autoDismiss: boolean;
  alone: boolean;
  onClose: () => void;
}

interface IToastState {
  time: number;
  visible: boolean;
}

const icons: any = {
  success: { prefix: 'far', iconName: 'check-circle' },
  danger: { prefix: 'far', iconName: 'exclamation-circle' },
  warning: { prefix: 'far', iconName: 'exclamation-circle' },
  primary: { prefix: 'far', iconName: 'info-square' },
};

const getIconBasedOnType = (type: string) => {
  const icon = icons[type];
  return icon ? <FaTimes /> : null;
};

class Toast extends React.Component<IToastProps, IToastState> {
  static defaultProps = {
    type: EToastType.Primary,
    time: 5000,
    autoDismiss: true,
    alone: true,
    onClose: () => {},
  };

  readonly state = {
    time: this.props.time,
    visible: true,
  };

  timer: number = 0;

  componentDidMount() {
    const { autoDismiss } = this.props;
    if (autoDismiss) {
      this.startTimer();
    }
  }

  componentWillUnmount() {
    const { autoDismiss } = this.props;
    if (autoDismiss) {
      clearInterval(this.timer);
    }
  }

  handleMouseLeave = () => {
    const { autoDismiss } = this.props;
    if (autoDismiss) {
      this.startTimer();
    }
  };

  onDismiss = () => {
    const { onClose } = this.props;
    this.setState({ visible: false });
    onClose();
  };

  handleMouseEnter = () => {
    const { autoDismiss } = this.props;
    if (autoDismiss) {
      this.pauseTimer();
    }
  };

  startTimer = () => {
    const { time } = this.state;
    const { onClose } = this.props;
    this.timer = window.setInterval(() => {
      this.setState({
        visible: false,
      });
      onClose();
    }, time);
  };

  pauseTimer = () => {
    window.clearInterval(this.timer);
  };

  render() {
    const { type, alone, message } = this.props;
    const { visible } = this.state;
    const toastClasses = classNames('toast', `toast-${type}`, {
      'toast-container': alone,
    });
    return (
      visible && (
        <div
          className={toastClasses}
          onMouseEnter={this.handleMouseEnter}
          onMouseLeave={this.handleMouseLeave}
        >
          <div className="toast__icon">
            <span aria-hidden="true">{getIconBasedOnType(type)}</span>
          </div>
          <div className="toast__content">{t(message)}</div>
          <div className="toast__action">
            <button type="button" className="close" aria-label="Close" onClick={this.onDismiss}>
              <span aria-hidden="true">
                <FaTimes />
              </span>
            </button>
          </div>
        </div>
      )
    );
  }
}

export default Toast;
