import { IGenericObject } from '../types/global';

export const UserStatus: IGenericObject = {
  ACTIVE: 'ACTIVE',
  BLOCKED: 'BLOCKED',
  DELETED: 'DELETED',
};

const Status = {
  ...UserStatus,
};

export const statusClasses = (originalStatus: string) => {
  if (!originalStatus) {
    return null;
  }
  const status = originalStatus.toUpperCase();
  return {
    'bg-success': [Status.ACTIVE].includes(status),
    'bg-secondary': [Status.BLOCKED].includes(status),
    'bg-warning': [Status.DELETED].includes(status),
  };
};
