import * as React from 'react';
import './ConfirmationModal.scss';
import { IModalConfirmationProps, EModalType } from '../../../../redux/global/type';
import { FaTimes } from 'react-icons/fa';
import { Button, EButtonType } from '../../../commons';
import { t } from '../../../../config/i18n';
import Modal from '../Modal';

const ConfirmationModal: React.FC<IModalConfirmationProps> = props => {
  const {
    title,
    confirmMessage,
    className,
    onClose,
    confirmAction,
    confirmBtnText = t('buttonConfirm'),
    cancelBtnText = t('buttonCancel'),
    afterCloseAction,
  } = props;
  const [submitting, setSubmitting] = React.useState(false);
  const onClickConfirm = async () => {
    setSubmitting(true);
    const response = await confirmAction();
    onClose && onClose();
    setSubmitting(false);
    // Necessary 'cause the hidding effect of modals, if any problem remove both timeouts
    if (afterCloseAction) {
      setTimeout(() => afterCloseAction(response), 345);
    }
  };
  return (
    <Modal className={className} title={title} type={EModalType.Warning} onClose={onClose}>
      <div className="confirm__modal">
        <div className="confirm__modal__body">
          <div className="confirm__modal__body__icon">
            <FaTimes />
          </div>
          <p>{confirmMessage}</p>
        </div>
        <footer>
          <div className="buttons-container">
            <Button
              buttonType={EButtonType.transparent}
              type="submit"
              onClick={onClickConfirm}
              disabled={submitting}
              loading={submitting}
            >
              {confirmBtnText}
            </Button>
            <Button buttonType={EButtonType.primary} onClick={onClose}>
              {cancelBtnText}
            </Button>
          </div>
        </footer>
      </div>
    </Modal>
  );
};

export default ConfirmationModal;
