import { createSelector } from 'reselect';
import { RootState } from 'ReduxTypes';
import { SessionState } from './reducer';

const getSessionState: (s: RootState) => SessionState = state => state.session;

export const getIsAuthenticated = createSelector(getSessionState, state => {
  return state.authenticated;
});

export const getUser: any = createSelector(getSessionState, state => {
  console.log(state.user);
  return state.user;
});

export const isLoading = createSelector(getSessionState, state => {
  return state.loading;
});
